import { usePathname } from "next/navigation";
import React, { use, useEffect } from "react";

interface TerminalProps {
  bottomPannelToggle: boolean;
}

function Terminal({ bottomPannelToggle }: TerminalProps) {
  const [activeTab, setActiveTab] = React.useState("PROBLEMS");
  const tabs: string[] = ["PROBLEMS", "OUTPUT", "DEBUG CONSOLE", "TERMINAL"];
  const [currentUrl, setCurrentUrl] = React.useState<string | null>(null);
  const [logs, setLogs] = React.useState<string[]>([]);

  useEffect(() => {
    const handler = (e: Event) => {
      const customEvent = e as CustomEvent<string>;
      setLogs((prevLogs) => [...prevLogs, `> ${customEvent.detail}`]);
    };
    window.addEventListener("terminal-log", handler);
    return () => window.removeEventListener("terminal-log", handler);
  }, []);

  useEffect(() => {
    setCurrentUrl(window.location.href);
  }, [1]);

  return (
    <div
      className={
        bottomPannelToggle
          ? "w-screen h-48 fixed bottom-0 right-0 bg-secondary-bg animate-[slideUp_0.2s_ease-out_forwards] ease-in-out duration-300 "
          : "hidden"
      }
    >
      <div className=" flex items-center w-screen h-8 bg-[var(--bg)] fixed px-10">
        {tabs.map((tab: string) => (
          <p
            className={`text-gray text-sm mr-8 ${
              activeTab == tab
                ? "border-b text-highlight-green border-highlight-green pb-1"
                : ""
            } cursor-pointer hover:text-white`}
            key={tabs.indexOf(tab)}
            onClick={() => {
              setActiveTab(tab);
            }}
          >
            {tab}
          </p>
        ))}
      </div>
      {activeTab === "PROBLEMS" && (
        <div className="text-text pt-10 px-10 text-sm ">
          No problems found in workspace.
        </div>
      )}
      {activeTab === "TERMINAL" && (
        <div className="px-10 text-text text-xs">
          <div className="text-text pt-10 ">
            Five Server <span className="text-cyan-200">running at:</span>
          </div>
          <div>
            Network : <span className="text-highlight-green">{currentUrl}</span>
          </div>
          {logs.map((log, i) => (
            <p key={i}>{log}</p>
          ))}
        </div>
      )}
    </div>
  );
}

export default Terminal;
